import React, { useState } from 'react';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Chip from '@mui/material/Chip';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import { useI18n } from '../context/I18nContext';
import Toast from '../components/Toast';

export const HomePage: React.FC = () => {
  const { locale } = useI18n();
  const c = locale === 'cs' ? require('../data/cs/contact.json') : require('../data/en/contact.json');
  const s = locale === 'cs' ? require('../data/cs/skills.json') : require('../data/en/skills.json');

  const techs: string[] = [ ...(s.tech || []), ...((s.tools as string[]) || []) ];
  const [copied, setCopied] = useState<string | null>(null);
  const [severity, setSeverity] = useState<'success' | 'error'>('success');

  const copyEmail = async (e: React.MouseEvent) => {
    e.preventDefault();
    const email: string = c?.email || '';
    try {
      if (navigator && 'clipboard' in navigator) {
        await (navigator as any).clipboard.writeText(email);
      } else {
        const ta = document.createElement('textarea');
        ta.value = email;
        ta.style.position = 'fixed';
        ta.style.opacity = '0';
        document.body.appendChild(ta);
        ta.focus();
        ta.select();
        document.execCommand('copy');
        document.body.removeChild(ta);
      }
      setSeverity('success');
      setCopied(locale === 'cs' ? 'E‑mail byl zkopírován do schránky.' : 'E‑mail copied to clipboard.');
    } catch {
      setSeverity('error');
      setCopied(locale === 'cs' ? 'Nepodařilo se zkopírovat e‑mail.' : 'Failed to copy e‑mail.');
    }
  };

  return (
    <>
    <section className="container" style={{ padding: '32px 0' }}>
      <Box sx={{
        p: { xs: 2, md: 3 },
        borderRadius: 4,
        background: 'linear-gradient(135deg, rgba(66,81,84,0.95) 0%, rgba(81,81,81,0.65) 50%, rgba(217,217,217,0.45) 100%)',
      }}>
        <Paper elevation={8} sx={{ p: { xs: 4, md: 6 }, backdropFilter: 'blur(8px)' }}>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={4} alignItems={{ xs: 'center', md: 'flex-start' }}>
            <Avatar src={"https://qwerjorsag.github.io/MyCV/assets/profile.jpg"} alt="Profile" sx={{ width: 140, height: 140, borderRadius: 8 }}>J</Avatar>
            <Stack spacing={1} sx={{ flex: 1 }}>
              <Typography variant="h2" fontWeight={900} sx={{ lineHeight: 1.1 }}>{c.fullName}</Typography>
              <Typography variant="h6" color="text.secondary">{c.location}</Typography>
              <Box sx={{ mt: 2 }}>
                <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ rowGap: 1.25 }}>
                  {techs.map((t: string, i: number) => (
                    <Chip
                      key={i}
                      label={t}
                      variant="filled"
                      sx={(theme) => ({
                        borderRadius: 9999,
                        bgcolor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.14)' : 'grey.100',
                        color: theme.palette.mode === 'dark' ? theme.palette.common.white : '#1b1b1b',
                        border: '1px solid',
                        borderColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.22)' : 'grey.200'
                      })}
                    />
                  ))}
                </Stack>
              </Box>
              <Divider sx={{ my: 2 }} />
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={1.5}>
                {c.email && (
                  <Button
                    href={`mailto:${c.email}`}
                    onClick={copyEmail}
                    startIcon={<MailOutlineIcon />}
                    size="large"
                    variant="contained"
                    color="primary"
                    title={locale === 'cs' ? 'Kliknutím zkopírujete e‑mail' : 'Click to copy e‑mail'}
                  >
                    E‑mail
                  </Button>
                )}
                {c.linkedin && (
                  <Button href={c.linkedin} target="_blank" rel="noreferrer" startIcon={<LinkedInIcon />} size="large" variant="contained" color="primary">
                    LinkedIn
                  </Button>
                )}
              </Stack>
            </Stack>
          </Stack>
        </Paper>
      </Box>

      <Stack spacing={2.5} sx={{ mt: 3 }}>
        <Paper sx={{ p: { xs: 2, md: 3 } }}>
          <Typography variant="h5" fontWeight={800} sx={{ mb: 1 }}>{locale === 'cs' ? 'O mně' : 'About me'}</Typography>
          <Typography sx={{ mb: 1.5 }}>
            {locale === 'cs'
              ? 'Jsem BI analytik a doktorand (BI + NLP) na ČZU, kde také vyučuji (datové sklady, vizualizace, ML v Pythonu). Mám zkušenosti s analýzou dat, modelováním, reporty, návrhem IS architektur a vývojem frontendů v Reactu.'
              : 'I am a BI analyst and Ph.D. student (BI + NLP) at CULS, where I also teach (data warehouses, visualization, ML in Python). I have experience in data analysis, modeling, reporting, IS architecture design and building frontends in React.'}
          </Typography>
          <Typography>
            {locale === 'cs'
              ? 'Pracoval jsem na projektech od ETL/ELT přes datové modelování a vizualizace až po QA a FE vývoj (React). Baví mě propojovat data s uživatelským rozhraním tak, aby výstupy byly srozumitelné a praktické.'
              : 'I have worked on projects from ETL/ELT and data modeling to visualization, QA, and frontend development (React). I enjoy connecting data with UI so that the outcomes are clear and practical.'}
          </Typography>
        </Paper>
      </Stack>
    </section>
    <Toast open={!!copied} message={copied} onClose={() => setCopied(null)} severity={severity} />
    </>
  );
};



