import React from 'react';
import csData from '../data/cs/certifications.json';
import enData from '../data/en/certifications.json';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Avatar from '@mui/material/Avatar';
import { useI18n } from '../context/I18nContext';

type Cert = { name: string; issuer: string; date: string; url?: string; description?: string; logo?: string };

export const CertificationsPage: React.FC = () => {
  const { locale } = useI18n();
  const items = (locale === 'cs' ? csData : enData) as unknown as Cert[];
  return (
    <section className="container" style={{ padding: '24px 0' }}>
      <Stack spacing={2}>
        {items.length === 0 && (
          <Paper sx={{ p: 2.5 }}>
            <Typography>Brzy doplním certifikace.</Typography>
          </Paper>
        )}
        {items.map((c, idx) => (
          <Paper key={idx} sx={{ p: 2.5 }}>
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} alignItems={{ xs: 'flex-start', sm: 'center' }}>
              {(c as any).logoPath || (c as any).logo ? (
                  <Avatar
                      src={
                          ((c as any).logoPath || (c as any).logo)
                              ? process.env.PUBLIC_URL + ((c as any).logoPath || (c as any).logo)
                              : undefined
                      }
                      alt={c.name}
                      sx={{ width: 48, height: 48, borderRadius: 2 }}
                  />

              ) : null}
              <Stack spacing={0.5} sx={{ flex: 1 }}>
                <Typography variant="h6" sx={{ m: 0 }}>{c.name}</Typography>
                <Typography variant="body2" color="text.secondary">{c.issuer}</Typography>
                {c.date && <Typography variant="body2">{c.date}</Typography>}
                {c.description && <Typography variant="body2">{c.description}</Typography>}
                {c.url && <Button href={c.url} target="_blank" rel="noreferrer" variant="outlined" sx={{ mt: 1, alignSelf: 'flex-start' }}>Detail</Button>}
              </Stack>
            </Stack>
          </Paper>
        ))}
      </Stack>
    </section>
  );
};
