import React, { createContext, useContext, useEffect, useMemo, useState } from 'react';
import { useConsent } from './ConsentContext';

export type Locale = 'cs' | 'en';

type Messages = Record<string, string>;
type Catalog = Record<Locale, Messages>;

const messages: Catalog = {
  cs: {
    home: 'Domů',
    education: 'Vzdělání',
    experience: 'Pracovní zkušenosti',
    projects: 'Projekty',
    certifications: 'Certifikace',
    contact: 'Kontakt',
    theme: 'Režim',
    language: 'Jazyk',
    light: 'Světlý',
    dark: 'Tmavý',
    cookie_title: 'Nastavení souborů cookie',
    cookie_text: 'Používáme pouze nezbytné a preferenční cookies (motiv: téma a jazyk). Analytické/marketingové neukládáme bez souhlasu.',
    accept_all: 'Přijmout vše',
    reject_nonessential: 'Odmítnout nepodstatné',
  },
  en: {
    home: 'Home',
    education: 'Education',
    experience: 'Experience',
    projects: 'Projects',
    certifications: 'Certifications',
    contact: 'Contact',
    theme: 'Theme',
    language: 'Language',
    light: 'Light',
    dark: 'Dark',
    cookie_title: 'Cookie preferences',
    cookie_text: 'We use only necessary and preference cookies (theme and language). No analytics/marketing without consent.',
    accept_all: 'Accept all',
    reject_nonessential: 'Reject non‑essential',
  }
};

type I18nContextType = {
  locale: Locale;
  t: (key: keyof typeof messages['cs']) => string;
  setLocale: (l: Locale) => void;
};

const I18nContext = createContext<I18nContextType | undefined>(undefined);
const LOCALE_KEY = 'pref-locale';

export const I18nProvider: React.FC<{ children: React.ReactNode }>
  = ({ children }) => {
  const { consent } = useConsent();
  const [locale, setLocaleState] = useState<Locale>('cs');

  useEffect(() => {
    if (consent?.preferences) {
      const persisted = localStorage.getItem(LOCALE_KEY) as Locale | null;
      if (persisted === 'cs' || persisted === 'en') {
        setLocaleState(persisted);
      }
    }
  }, [consent?.preferences]);

  const setLocale = (l: Locale) => {
    setLocaleState(l);
    if (consent?.preferences) {
      try { localStorage.setItem(LOCALE_KEY, l); } catch { /* ignore */ }
    }
  };

  const t = (key: keyof typeof messages['cs']) => messages[locale][key] ?? key;
  const value = useMemo(() => ({ locale, t, setLocale }), [locale]);
  return <I18nContext.Provider value={value}>{children}</I18nContext.Provider>;
};

export const useI18n = () => {
  const ctx = useContext(I18nContext);
  if (!ctx) throw new Error('useI18n must be used within I18nProvider');
  return ctx;
};

