import React from 'react';
import csData from '../data/cs/projects.json';
import enData from '../data/en/projects.json';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Chip from '@mui/material/Chip';
import { useI18n } from '../context/I18nContext';

type Project = {
  name: string;
  summary: string;
  url?: string; // legacy: demo/link URL
  repo?: string;
  tech?: string[];
  highlights?: string[];
  demo?: string; // optional alias for demo URL
  link?: string; // optional alias for any link
  hideLink?: boolean; // when true, suppress Link button even if URL present
};

export const ProjectsPage: React.FC = () => {
  const { locale } = useI18n();
  const items = (locale === 'cs' ? csData : enData) as unknown as Project[];
  return (
    <section className="container" style={{ padding: '24px 0' }}>
      <Stack spacing={2}>
        {items.length === 0 && (
          <Paper sx={{ p: 2.5 }}>
            <Typography>Brzy doplním projekty.</Typography>
          </Paper>
        )}
        {items.map((p, idx) => (
          <Paper key={idx} sx={{ p: 2.5 }}>
            <Typography variant="h5" sx={{ m: 0 }}>{p.name}</Typography>
            <Typography sx={{ mt: 1 }}>{p.summary}</Typography>
            {p.tech && p.tech.length > 0 && (
              <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ mt: 1, rowGap: 1 }}>
                {p.tech.map((t, i) => (
                  <Chip key={i} label={t} size="small" sx={(theme) => ({
                    borderRadius: 9999,
                    bgcolor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.14)' : 'grey.100',
                    color: theme.palette.mode === 'dark' ? theme.palette.common.white : '#1b1b1b',
                    border: '1px solid',
                    borderColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.22)' : 'grey.200'
                  })} />
                ))}
              </Stack>
            )}
            {p.highlights && p.highlights.length > 0 && (
              <ul style={{ marginTop: 8 }}>
                {p.highlights.map((h, i) => (
                  <li key={i}>{h}</li>
                ))}
              </ul>
            )}
            <Stack direction="row" spacing={1.5} sx={{ mt: 1 }}>
              {(() => {
                const linkUrl = p.url || (p as any).demo || (p as any).link;
                if (!linkUrl || (p as any).hideLink) return null;
                return (
                  <Button href={linkUrl} target="_blank" rel="noreferrer" variant="contained">Link</Button>
                );
              })()}
            </Stack>
          </Paper>
        ))}
      </Stack>
    </section>
  );
};
