import React, { useEffect, useMemo, useState } from 'react';
import './styles/theme.css';
import { Navbar } from './components/Navbar';
import { Footer } from './components/Footer';
import { CookieConsent } from './components/CookieConsent';
import { ConsentProvider } from './context/ConsentContext';
import { ThemeProvider } from './context/ThemeContext';
import { I18nProvider, useI18n } from './context/I18nContext';
import { HomePage } from './pages/HomePage';
import { EducationPage } from './pages/EducationPage';
import { ExperiencePage } from './pages/ExperiencePage';
import { ProjectsPage } from './pages/ProjectsPage';
import { CertificationsPage } from './pages/CertificationsPage';
import { ContactPage } from './pages/ContactPage';

type RouteKey = 'home' | 'education' | 'experience' | 'projects' | 'certifications' | 'contact';

const routeFromHash = (): RouteKey => {
  const h = (typeof window !== 'undefined' ? window.location.hash : '') || '#/';
  const path = h.replace(/^#\//, '').trim();
  if (path === '' || path === '/') return 'home';
  if (path.startsWith('education')) return 'education';
  if (path.startsWith('experience')) return 'experience';
  if (path.startsWith('projects')) return 'projects';
  if (path.startsWith('certifications')) return 'certifications';
  if (path.startsWith('contact')) return 'contact';
  return 'home';
};

function App() {
  const [route, setRoute] = useState<RouteKey>(routeFromHash());
  useEffect(() => {
    const onHash = () => setRoute(routeFromHash());
    window.addEventListener('hashchange', onHash);
    return () => window.removeEventListener('hashchange', onHash);
  }, []);

  const Page = useMemo(() => {
    switch (route) {
      case 'education': return <EducationPage />;
      case 'experience': return <ExperiencePage />;
      case 'projects': return <ProjectsPage />;
      case 'certifications': return <CertificationsPage />;
      case 'contact': return <ContactPage />;
      default: return <HomePage />;
    }
  }, [route]);

  return (
    <ConsentProvider>
      <I18nProvider>
        <ThemeProvider>
          {React.createElement(require('./theme/AppThemeProvider').AppThemeProvider, null,
            <div className="app-shell">
              <TitleManager current={route} />
              <Navbar active={route} />
              <main>
                {Page}
              </main>
              <Footer />
              <CookieConsent />
            </div>
          )}
        </ThemeProvider>
      </I18nProvider>
    </ConsentProvider>
  );
}

const TitleManager: React.FC<{ current: RouteKey }> = ({ current }) => {
  const { t } = useI18n();
  useEffect(() => {
    const base = 'Jiří Orság';
    const map: Record<RouteKey, string> = {
      home: base,
      education: `${base} - ${t('education')}`,
      experience: `${base} - ${t('experience')}`,
      projects: `${base} - ${t('projects')}`,
      certifications: `${base} - ${t('certifications')}`,
      contact: `${base} - ${t('contact')}`,
    };
    document.title = map[current] || base;
  }, [current, t]);
  return null;
};

export default App;

