import React from "react";
import csData from "../data/cs/experience.json";
import enData from "../data/en/experience.json";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import { useI18n } from "../context/I18nContext";

type ExpItem = {
  company: string;
  role: string;
  start: string;
  end: string | null;
  bullets?: string[];
};

export const ExperiencePage: React.FC = () => {
  const { locale } = useI18n();
  const items = (locale === "cs" ? csData : enData) as unknown as ExpItem[];
  const sorted = [...items].sort((a, b) => {
    const ya = a.end ? parseInt(a.end) : 9999;
    const yb = b.end ? parseInt(b.end) : 9999;
    if (yb !== ya) return yb - ya;
    const sa = parseInt(a.start);
    const sb = parseInt(b.start);
    return sb - sa;
  });
  return (
    <section className="container" style={{ padding: "24px 0" }}>
      <Stack spacing={2}>
        {sorted.map((e, idx) => (
          <Paper key={idx} sx={{ p: 2.5 }}>
            <Typography variant="h5" sx={{ m: 0 }}>
              {e.role}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {e.company}
            </Typography>
            <Typography variant="body2" sx={{ mt: 1 }}>
              {e.start} – {e.end ?? (locale === "cs" ? "současnost" : "present")}
            </Typography>
            {e.bullets?.length ? (
              <ul>
                {e.bullets.map((n, i) => (
                  <li key={i}>{n}</li>
                ))}
              </ul>
            ) : null}
          </Paper>
        ))}
      </Stack>
    </section>
  );
};
