import React, { useState } from 'react';
import csData from '../data/cs/contact.json';
import enData from '../data/en/contact.json';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import TextField from '@mui/material/TextField';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';
import { useI18n } from '../context/I18nContext';
import Toast from '../components/Toast';

export const ContactPage: React.FC = () => {
  const { locale } = useI18n();
  const c = (locale === 'cs' ? csData : enData) as any;
  const [message, setMessage] = useState('');
  const [open, setOpen] = useState(false);
  const [copied, setCopied] = useState<string | null>(null);
  const [severity, setSeverity] = useState<'success' | 'error'>('success');

  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setOpen(true);
  };

  const copyEmail = async (e: React.MouseEvent) => {
    e.preventDefault();
    const email: string = c?.email || '';
    try {
      if (navigator && 'clipboard' in navigator) {
        await (navigator as any).clipboard.writeText(email);
      } else {
        const ta = document.createElement('textarea');
        ta.value = email;
        ta.style.position = 'fixed';
        ta.style.opacity = '0';
        document.body.appendChild(ta);
        ta.focus();
        ta.select();
        document.execCommand('copy');
        document.body.removeChild(ta);
      }
      setSeverity('success');
      setCopied(locale === 'cs' ? 'E‑mail byl zkopírován do schránky.' : 'E‑mail copied to clipboard.');
    } catch {
      setSeverity('error');
      setCopied(locale === 'cs' ? 'Nepodařilo se zkopírovat e‑mail.' : 'Failed to copy e‑mail.');
    }
  };

  return (
    <section className="container" style={{ padding: '24px 0' }}>
      <Paper sx={{ p: { xs: 2, md: 3 } }}>
        <form onSubmit={onSubmit}>
          <Stack spacing={2}>
            <Typography variant="h4" sx={{ m: 0 }}>
              {locale === 'cs' ? 'Napište mi' : 'Write to me'}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {locale === 'cs'
                ? 'Pošlete mi prosím zprávu a co nejdříve Vám odpovím.'
                : 'Please send me a message and I will get back to you as soon as possible.'}
            </Typography>
            <TextField
              label={locale === 'cs' ? 'Zpráva' : 'Message'}
              placeholder={locale === 'cs' ? 'Napište mi zprávu…' : 'Write me a message…'}
              multiline minRows={4}
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              fullWidth
              sx={{
                '& .MuiOutlinedInput-root': {
                  backgroundColor: (theme) => theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.10)' : '#ffffff',
                  '& fieldset': {
                    borderColor: (theme) => theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.45)' : 'rgba(0,0,0,0.23)'
                  },
                  '&:hover fieldset': {
                    borderColor: (theme) => theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : 'rgba(0,0,0,0.35)'
                  },
                  '&.Mui-focused fieldset': {
                    borderColor: (theme) => theme.palette.mode === 'dark' ? '#ffffff' : '#425154'
                  }
                },
                '& .MuiInputBase-input': {
                  color: (theme) => theme.palette.mode === 'dark' ? '#ffffff' : 'inherit'
                }
              }}
            />
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={1.5}>
              <Button type="submit" variant="contained" color="primary">
                {locale === 'cs' ? 'Odeslat' : 'Send'}
              </Button>
              {c.email && (
                <Button href={`mailto:${c.email}`} onClick={copyEmail} variant="outlined" startIcon={<MailOutlineIcon />}
                  sx={{
                    color: (theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.primary.main,
                    borderColor: (theme) => theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : theme.palette.primary.main,
                    '&:hover': {
                      borderColor: (theme) => theme.palette.mode === 'dark' ? '#ffffff' : theme.palette.primary.dark,
                      backgroundColor: (theme) => theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.08)' : 'rgba(66,81,84,0.06)'
                    }
                  }}
                >
                  {locale === 'cs' ? 'E-mail' : 'E-mail'}
                </Button>
              )}
            </Stack>
          </Stack>
        </form>
      </Paper>
      <Snackbar open={open} onClose={() => setOpen(false)} autoHideDuration={5000} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
        <Alert onClose={() => setOpen(false)} severity="warning" variant="filled" sx={{ width: '100%' }}>
          {locale === 'cs'
            ? `Backend část není implementována. Prosím, kontaktujte mě na e-mailu ${c.email}.`
            : `Backend part is not implemented. Please contact me at ${c.email}.`}
        </Alert>
      </Snackbar>
      <Toast open={!!copied} message={copied} onClose={() => setCopied(null)} severity={severity} />
    </section>
  );
};


